<template>
  <Modal
    name="check"
    :isVisible="isVisible"
    @onClose="$emit('onClose')"
    @onClickMark="$emit('onClose')"
    :hasCloseButton="modalKey !== 'check'"
  >
    <div v-if="modalKey === 'check'">
      <div class="flex flex-col gap-8 items-center justify-center">
        <div class="w-20 h-20">
          <img src="@/assets/warn.png" />
        </div>
        <p class="text-3xl font-medium">18</p>
        <ButtonGroup
          :isCheck="isCheck"
          @onOk="$emit('onOk')"
          @onClose="$emit('onClose')"
          @onCheck="handleCheck"
          @onClickItem="handleClickItem"
        />
      </div>
    </div>
    <div v-if="modalKey === 'policy'" key="policy">
      <p class="text-3xl font-medium text-center mb-6"></p>
      <div
        class="text-2xl h-96 overflow-scroll lg:px-5 flex flex-col items-center gap-6"
      >
        
        
        <ButtonGroup
          :isCheck="isCheck"
          @onOk="$emit('onOk')"
          @onClose="$emit('onClose')"
          @onCheck="handleCheck"
          @onClickItem="handleClickItem"
        />
      </div>
    </div>
    <div v-if="modalKey === 'service'" key="service">
      <p class="text-3xl font-medium text-center mb-6"></p>
      <div
        class="text-2xl h-96 overflow-scroll lg:px-5 flex flex-col items-center gap-6"
      >
        
        
        <ButtonGroup
          :isCheck="isCheck"
          @onOk="$emit('onOk')"
          @onClose="$emit('onClose')"
          @onCheck="handleCheck"
          @onClickItem="handleClickItem"
        />
      </div>
    </div>
  </Modal>
</template>
<script>
import Modal from "@/components/Modal";
import ButtonGroup from "./ButtonGroup.vue";
import { mapGetters } from "vuex";

export default {
  components: {
    Modal,
    ButtonGroup,
  },
  data() {
    return {
      modalKey: "check",
      isCheck: false,
    };
  },
  computed: {
    ...mapGetters(["isGlobalCheck"]),
    isVisible() {
      return !this.isGlobalCheck;
    },
  },
  methods: {
    handleClickItem(modalKey) {
      this.modalKey = modalKey;
      this.isCheck = false;
    },
    handleCheck() {
      this.isCheck = !this.isCheck;
    },
  },
};
</script>
