<template>
  <footer
    class="flex lg:hidden px-4 py-2.5 flex-wrap border-t-1 border-solid border-border-color text-link"
    :class="{
      'pb-10': hasFixedPagination,
    }"
  >
    <router-link
      v-for="{ path, name } in links"
      :key="name"
      :to="path"
      class="mr-4 mb-2"
    >
      {{ name }}
    </router-link>
  </footer>
</template>

<script>
export default {
  data() {
    return {
      links: [
        { path: "/system/link", name: "" },
        { path: "/system/policy", name: "" },
        { path: "/system/service", name: "" },
        { path: "/system/privacy", name: "" },
        { path: "/system/disclaimer", name: "" },
        { path: "/system", name: "" },
      ],
    };
  },
  computed: {
    hasFixedPagination() {
      const hasFixedPaginationList = [
        "/picture/detail/",
        "/novel/detail/",
        "/comic-book/content/",
      ];
      const fullPath = this.$route.fullPath;
      return !!hasFixedPaginationList.find((item) => fullPath.includes(item));
    },
  },
};
</script>
